import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import { combineReducers, createStore } from 'redux';

const alertState = true;

function reducer2(state = alertState, action) {
  if (action.type === 'CLOSEALERT') {
    state = false;
    return state;
  } else {
    return state;
  }
} 

const defaultState =  [
    {
      id : 0,
      name : '멋진신발',
      quan : 2
    },
    {
      id : 1,
      name : '된장골드',
      quan : 1
    }
  ]

function reducer( state = defaultState, action){
  if (action.type === 'ADDITEMCART') {
    let found = state.findIndex((a)=>{ return a.id === action.payload.id});
    if (found >= 0) {
      let copyData = [...state];
      copyData[found].quan++;
      return copyData;
    } else {
      let copyData = [...state];
      copyData.push(action.payload);
      return copyData;
    }
  } else if ( action.type === 'INCREASE') {
    let copyData = [...state];
    copyData[action.numData].quan++;
    return copyData;
  } else if ( action.type === 'REDUCTION') {
    let copyData = [...state];
    if(copyData[action.numData].quan > 0) {
      copyData[action.numData].quan--;
    }
    return copyData;
  } else {
    return state;
  }
}

const store = createStore(combineReducers({reducer,reducer2}));

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
